___CSS_LOADER_EXPORT___.push([module.id, `* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

.flex {
  display: flex;
  align-items: center;
  justify-content: center;
}

body {
  font-family: 'Pacifico', 'Yanone Kaffeesatz', 'Acme', cursive, monospace, sans-serif, serif;
  overflow-x: hidden;
  margin: 0;
  min-height: 100vh;
}

.max-w {
  max-width: 1200px;
  margin: auto;
}

header {
  padding: 0 10px;
}

.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 100%;
}

.logo {
  width: 60px;
  height: 60px;
}

.nav-items {
  display: flex;
  justify-content: space-around;
  align-items: center;
  gap: 10px;
}

.nav-link {
  list-style-type: none;
}

.container {
  flex-wrap: wrap;
  align-content: center;
  gap: 20px;
  padding: 20px 0;
}

.card {
  width: 350px;
  height: 460px;
  padding-bottom: 20px;
  flex-direction: column;
  border: 1px solid gainsboro;
  border-radius: 15px;
  gap: 0;
  margin: 0 5px;
  background-color: gainsboro;
}

.card h1 {
  text-align: center;
}

.card img {
  width: 200px;
  border-radius: 15px;
}

.card button {
  border: 1px solid #ffeb3b;
  border-radius: 5px;
  height: 40px;
  width: 90px;
  background: #ff9800;
  color: white;
  margin: 5px 0;
}

footer {
  height: 50px;
}

footer h3 {
  text-align: center;
}

/* Reservation */
.modal{
  width: 1200px;
  background: black;
}
.Reservation-modal{
  position: fixed;
  background: linear-gradient(rgba(193, 181, 181, 0.2), rgba(218, 204, 204, 0.4));
  max-width: 80vw;
  height:98vh;
  margin: 1rem auto;
  border: 4px solid blue;
  position: absolute;
  top: 48%;
  left: 50%;
  z-index: 8;
  /* background-color: rgb(226, 213, 213); */
  overflow-y: hidden;
  display: block;
  animation-name: modalOpen;
  animation-duration: 5s;
  transform: translate(-50%, -50%);
}

@keyframes modalOpen{
  from{
    opacity: 0;
  }
  to{
    opacity: 1;
  }
}


#closeBtn{
  width:64;
	height:64;
  position: absolute;
  z-index: 1;
  right: 1.3rem;
  margin-top: 1rem;
  margin-left: 1rem;
  border: 4px solid red;
}

.meal-container-order{
  max-width: 70%;
  margin: 1rem auto;
  border: 4px solid green;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items:center;
  border-radius: 3%;
}

.img-container{
  border: 4px solid red;
  width: 50%;
  display: flex;
  justify-content: space-between;
  align-items:center;
  margin: 0.5rem auto;
}

.item-img{
  width:80%;
}

.item-specification-container{
  border: 4px solid blue;
  display: flex;
  justify-content: space-between;
  align-items:center;
  width: 80%;
  padding: 1rem;
  margin: 0.5rem 0 0.5rem 0;
}

.item-specification{
  list-style-type: none;
}
.item{
  margin-bottom: 0.5rem;
  font-size: 1.2rem;
}


.order-list{
  width: 100%;
  border: 4px solid green;
  margin: 0.5rem 0 0.5rem 0;
  padding: 1rem;
  list-style-type: none;
}

.order-form{
  border:4px solid red;
  width: 70%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items:center;
  padding: 0.5rem;
  margin-bottom:0.5rem ;
}


.form-control{
  width: 80%;
  margin-bottom: 1rem;
}

.form-control,input[type='text']{
  padding: 0.5rem;
}

.form-control:focus{
  outline: none;
}

.btnReserve{
  padding: 0.5rem;
  background-color: blue;
  color: #fff;
  border-radius: 0.4rem;
  border: none;
  font-size: 1.5rem;
}`, "",{"version":3,"sources":["webpack://./src/style.css"],"names":[],"mappings":"AAIA;EACE,sBAAsB;EACtB,UAAU;EACV,SAAS;AACX;;AAEA;EACE,aAAa;EACb,mBAAmB;EACnB,uBAAuB;AACzB;;AAEA;EACE,2FAA2F;EAC3F,kBAAkB;EAClB,SAAS;EACT,iBAAiB;AACnB;;AAEA;EACE,iBAAiB;EACjB,YAAY;AACd;;AAEA;EACE,eAAe;AACjB;;AAEA;EACE,aAAa;EACb,8BAA8B;EAC9B,mBAAmB;EACnB,YAAY;AACd;;AAEA;EACE,WAAW;EACX,YAAY;AACd;;AAEA;EACE,aAAa;EACb,6BAA6B;EAC7B,mBAAmB;EACnB,SAAS;AACX;;AAEA;EACE,qBAAqB;AACvB;;AAEA;EACE,eAAe;EACf,qBAAqB;EACrB,SAAS;EACT,eAAe;AACjB;;AAEA;EACE,YAAY;EACZ,aAAa;EACb,oBAAoB;EACpB,sBAAsB;EACtB,2BAA2B;EAC3B,mBAAmB;EACnB,MAAM;EACN,aAAa;EACb,2BAA2B;AAC7B;;AAEA;EACE,kBAAkB;AACpB;;AAEA;EACE,YAAY;EACZ,mBAAmB;AACrB;;AAEA;EACE,yBAAyB;EACzB,kBAAkB;EAClB,YAAY;EACZ,WAAW;EACX,mBAAmB;EACnB,YAAY;EACZ,aAAa;AACf;;AAEA;EACE,YAAY;AACd;;AAEA;EACE,kBAAkB;AACpB;;AAEA,gBAAgB;AAChB;EACE,aAAa;EACb,iBAAiB;AACnB;AACA;EACE,eAAe;EACf,+EAA+E;EAC/E,eAAe;EACf,WAAW;EACX,iBAAiB;EACjB,sBAAsB;EACtB,kBAAkB;EAClB,QAAQ;EACR,SAAS;EACT,UAAU;EACV,0CAA0C;EAC1C,kBAAkB;EAClB,cAAc;EACd,yBAAyB;EACzB,sBAAsB;EACtB,gCAAgC;AAClC;;AAEA;EACE;IACE,UAAU;EACZ;EACA;IACE,UAAU;EACZ;AACF;;;AAGA;EACE,QAAQ;CACT,SAAS;EACR,kBAAkB;EAClB,UAAU;EACV,aAAa;EACb,gBAAgB;EAChB,iBAAiB;EACjB,qBAAqB;AACvB;;AAEA;EACE,cAAc;EACd,iBAAiB;EACjB,uBAAuB;EACvB,aAAa;EACb,sBAAsB;EACtB,8BAA8B;EAC9B,kBAAkB;EAClB,iBAAiB;AACnB;;AAEA;EACE,qBAAqB;EACrB,UAAU;EACV,aAAa;EACb,8BAA8B;EAC9B,kBAAkB;EAClB,mBAAmB;AACrB;;AAEA;EACE,SAAS;AACX;;AAEA;EACE,sBAAsB;EACtB,aAAa;EACb,8BAA8B;EAC9B,kBAAkB;EAClB,UAAU;EACV,aAAa;EACb,yBAAyB;AAC3B;;AAEA;EACE,qBAAqB;AACvB;AACA;EACE,qBAAqB;EACrB,iBAAiB;AACnB;;;AAGA;EACE,WAAW;EACX,uBAAuB;EACvB,yBAAyB;EACzB,aAAa;EACb,qBAAqB;AACvB;;AAEA;EACE,oBAAoB;EACpB,UAAU;EACV,aAAa;EACb,sBAAsB;EACtB,8BAA8B;EAC9B,kBAAkB;EAClB,eAAe;EACf,qBAAqB;AACvB;;;AAGA;EACE,UAAU;EACV,mBAAmB;AACrB;;AAEA;EACE,eAAe;AACjB;;AAEA;EACE,aAAa;AACf;;AAEA;EACE,eAAe;EACf,sBAAsB;EACtB,WAAW;EACX,qBAAqB;EACrB,YAAY;EACZ,iBAAiB;AACnB","sourcesContent":["@import url('https://fonts.googleapis.com/css2?family=Paci